.partners {
}

.partners-item {
    border: 1px solid #eee;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    margin: 10px;
    height: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
        filter: grayscale(100%);
        transition: all .2s linear;
        width: 100%;
    }
    &:hover {
        img {
            filter: grayscale(0%);
            transition: all .2s linear;
        }
    }
}