.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    z-index: 99999;
    display: none;
    visibility: hidden;
    transition: all .5s linear;
    &-inner {
        visibility: hidden;
        position: relative;
        background-color: #fff;
        max-width: 700px;
        width: 100%;
        position: absolute;
        left: 50%;
        top: 20%;
        margin-left: -350px;
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s linear;
        transform: scale(.5);
        @media(max-width: 460px) {
            margin: 0;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }
        .close {
            position: absolute;
            top: 10px;
            right: 10px;
            width: 20px;
            height: 20px;
            cursor: pointer;
            z-index: 999;
            img {
                width: 100%;
            }
        }
    }
}

.show {
    display: block;
    visibility: visible;
    transition: all .5s linear;
    .modal-inner {
        transform: scale(1);
        visibility: visible;
        transition: all .5s linear;
    }
}
.modal-video-trigger {
    .modal-inner {
        padding: 0;
        width: auto;
        height: auto;
        background-color: transparent;
        @media(max-width: 460px) {
            margin: 0;
            left: 0;
            right: 0;
            max-width: 100%;
        }
    }
    
}

