// Global Start!//

main {
    width: 100%;
    margin: 0 auto
}
* {
    color: #292930;
}
section {
    @media(max-width: 1280px) {
        overflow-x: hidden;
    }
}

body {
    background: linear-gradient(180deg, rgba(0, 116, 188, 0) 0%, rgba(0, 116, 188, 0.13) 100%);
}

.section-full-width {
    width: 100%;
    padding-bottom: 80px;
    @media (max-width: 620px) {
        padding-bottom: 40px;
    }
}
.section-semi-width {
    max-width: 1200px;
    margin: 0 auto;
    padding-right: 10px;
    padding-left: 10px;
    padding-bottom: 80px;
    @media (max-width: 620px) {
        padding-bottom: 40px;
    }
}
.section-banner {
    padding-bottom: 0;
}
a {
    text-decoration: none;
    color: #000;
}

.grey-bg {
    background-color: transparent;
}
button {
    cursor: pointer;
}
.faded {
    position: relative;
    overflow: hidden;
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 116, 188, 0.13);
        z-index: 1;
        transition: all .5s linear;
        z-index: 1;
    }
    &:hover {
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(0, 116, 188, 0.23);
            z-index: 1;
            transition: all .5s linear;
            z-index: 1;
        }
    }
}
.section-heading {
    max-width: 1280px;
    margin: 0 auto;
    padding-top: 0;
    padding-bottom: 30px;
    font-size: 42px;
    font-weight: normal;
    @media(max-width: 768px) {
        font-size: 24px;
    }
}

.cutoff-text-block {
    font-size: 14px;

    --max-lines: 5;
    --line-height: 1.2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: var(--max-lines);

    overflow: hidden;
    max-height: calc(var(--max-lines) * 1em * var(--line-height));
    position: relative;
    &::before {
        content: '';
        position: absolute;
        height: calc(1em * var(--line-height));
        width: 100%;
        bottom: 0;
        pointer-events: none;
        background: linear-gradient(to bottom, transparent, white);
    }
}

h2 {
    padding-left: 10px;
    padding-right: 10px;
    @media(max-width: 1024px) {
        text-align: center;
    }
}

.slider-img {
    img {
        object-fit: cover;
        width: 100%;
        height: auto;
    }
}

.button-call {
    background-color: #ED1C24;
    color: #fff;
    padding: 10px;
    border: none;
    width: 180px;
    height: 50px;
    font-size: 16px;
    transition: all .2s linear;
    border-radius: 3px;
    &:hover {
        background-color: rgb(230, 156, 158);
        transition: all .2s linear;
    }
}

.collback-form-help {
    display: flex;
    flex-direction: column;
    max-width: 450px;
    width: 100%;
    padding: 50px 20px;
    background-color: #fff;
    border: 1px solid #eee;
    border-radius: 5px;
    @media(max-width: 460px) {
        max-width: 100%;
        height: 100%;
    }
    h4 {
        font-size: 20px;
        font-weight: normal;
        padding: 20px 0;
        padding-top: 0;
    }
    input {
        height: 50px;
        border: 1px solid #eee;
        margin-bottom: 30px;
        padding: 10px;
        border-radius: 5px;
    }
    button {
        background-color: #ED1C24;
        border: none;
        color: #fff;
        padding: 10px 20px;
        border-radius: 3px;
    }
}

.body-scroll-block {
    overflow-y: hidden;
}

.btn-animate {
    border: none;
    background: rgb(251,33,117);
    background: linear-gradient(0deg, #FF4242 0%, rgba(255,66,66,1) 100%);
    color: #fff;
    overflow: hidden;
    position: relative;
    padding: 10px 30px;
    border-radius: 3px;
    color: #fff;
    font-size: 16px;
  }
.btn-animate:hover {
    text-decoration: none;
    color: #fff;
}
  .btn-animate:before {
      position: absolute;
      content: '';
      display: inline-block;
      top: -180px;
      left: 0;
      width: 30px;
      height: 100%;
      background-color: #fff;
      animation: shiny-btn1 3s ease-in-out infinite;
  }
  .btn-animate:hover{
    opacity: .7;
  }
  .btn-animate:active{
    box-shadow:  4px 4px 6px 0 rgba(255,255,255,.3),
                -4px -4px 6px 0 rgba(116, 125, 136, .2), 
      inset -4px -4px 6px 0 rgba(255,255,255,.2),
      inset 4px 4px 6px 0 rgba(0, 0, 0, .2);
  }
  
  
  @-webkit-keyframes shiny-btn1 {
      0% { -webkit-transform: scale(0) rotate(45deg); opacity: 0; }
      80% { -webkit-transform: scale(0) rotate(45deg); opacity: 0.5; }
      81% { -webkit-transform: scale(4) rotate(45deg); opacity: 1; }
      100% { -webkit-transform: scale(50) rotate(45deg); opacity: 0; }
  }

  .btn-animate.blue-btn {
    border: none;
    background: #0074BC;
    background: linear-gradient(0deg, #0074BC 0%, #0074BC 100%);
    color: #fff;
    overflow: hidden;
    position: relative;
    padding: 10px 30px;
    border-radius: 3px;
    color: #fff;
    font-size: 16px;
  }


.mouse-parallax {
    height: 430px;
    position: relative;
    overflow: hidden;
    width: 100vw;
}
.mouse-parallax-bg {
    position: absolute;
    width: 101%;
    height: 101%;
    transition: all 0.1s ease;  
}

.aviation-parallax {
    background-image: url("../../img/aviation-banner-1.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.energy-parallax {
    background-image: url("../../img/energy-banner-1.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.electronics-parallax {
    background-image: url("../../img/electronics-banner-4.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

// Global End!//