.faq {
    margin-top: 80px;
    padding-top: 50px;
}
.faq-inner {
    max-width: 1200px;
    position: relative;
    min-height: 400px;
    margin: 0 auto;
    background-image: url('../../../img/Background-call-3.svg');
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    border-radius: 5px;
    box-shadow: 0 0 15px 2px rgba(0,0,0,.2);
    @media(max-width: 999px) {
        padding: 0 10px;
    }
}

.maskot-call {
    position: absolute;
    right: -45px;
    top: -10px;
    z-index: 999;
}

.collback-form {
    display: flex;
    flex-direction: column;
    max-width: 450px;
    width: 100%;
    padding: 50px 20px;
    background-color: #fff;
    border: 1px solid #eee;
    position: absolute;
    border-radius: 5px;
    right: 20%;
    top: -30px;
    box-shadow: 0 0 20px 2px rgba(0, 0, 0, .3);
    @media(max-width: 999px) {
        right: 50%;
        margin-right: -225px;
    }
    @media(max-width: 620px) {
        position: relative;
        right: 0;
        left: 0;
        margin: 0 auto;
        .maskot-call {
            display: none;
        }
    }
    h4 {
        font-size: 20px;
        font-weight: normal;
        padding: 20px 0;
        padding-top: 0;
    }
    input {
        height: 50px;
        border: 1px solid #eee;
        margin-bottom: 30px;
        padding: 10px;
        border-radius: 5px;
    }
    button {
        background-color: #ED1C24;
        border: none;
        color: #fff;
        padding: 10px 20px;
        border-radius: 3px;
    }
}

.questions-text-container {
    padding: 50px;
    max-width: 450px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding-top: 100px;
    h4 {
        font-size: 32px;
        margin-bottom: 20px;
        color: #fff;
    }
    p {
        font-size: 16px;
        color: #fff;
    }
    @media(max-width: 999px) {
        display: none;
    }
}

.call-container {
    position: relative;
    height: 100%;
    img {
        width: 100%;
    }
}