.news-slider-item {
    background-color: #fff;
    border-radius: 5px;
    padding: 10px;
    max-width: 420px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;
    transition: all .2s linear;
    margin: 10px;
    &:hover {
        .item-info__heading {
            color: #FF4242;
            transition: all .2s linear;
        }
        transform: scale(0.988);
    }
    // &:not(:last-child) {
    //     margin-right: 10px;
    // }
    img {
        max-width: 100%;
        height: auto;
        border-radius: 5px;
        margin-bottom: 10px;
    }
}
.item-info__heading {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.item-info__text {
    font-size: 14px;
    margin-bottom: 20px;
}

.news-btn {
    width: 100%;
}

.slick-prev-news {
    left: -20px;
    @media(max-width: 1400px) {
        left: 0;
    }
}
.slick-next-news {
    right: -20px;
    @media(max-width: 1400px) {
        right: 0;
    }
}