.choose-option-wrapper {
    margin-top: 50px;
    @media(max-width: 768px) {
        padding: 20px;
        margin-top: 0;
    }
}
.choose-option-inner {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    position: relative;
    padding: 30px 80px;
    align-items: center;
    border-radius: 5px;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, .2);
    &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-image: url('../../../img/callback-help-trigger-bg.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
    }
    @media(max-width: 1024px) {
        padding: 30px 20px;
        margin-right: 50px;
        margin-left: 50px;
    }
    @media(max-width: 620px) {
        flex-direction: column-reverse;
        margin-right: 0px;
        margin-left: 0px;
    }
}
.choose-option-circle {
    position: absolute;
    left: -10%;
    top: -30%;
}

.choose-option-button {
    display: flex;
    align-items: center;
    justify-content: center;
    @media(max-width: 620px) {
        margin-top: 30px;
        width: 100%;
        button {
            width: 100%;
        }
    }
}

.choose-option-item {
    color: #fff;
    h4 {
        font-size: 32px;
        padding-bottom: 20px;
        color: #fff;
        @media(max-width: 1180px) {
            font-size: 24px;
            color: rgba(41, 41, 48, 1);
        }
        @media(max-width: 1024px) {
            font-size: 24px;
        }
        @media(max-width: 620px) {
            font-size: 18px;
        }
    }
    p {
        margin-bottom: 20px;
        color: #fff;
        @media(max-width: 1180px) {
            font-size: 14px;
            color: rgba(41, 41, 48, 1);
        }
    }
    &:first-child {
        max-width: 300px;
        margin-left: 50px;
        width: 100%;
        z-index: 1;
        @media(max-width: 1024px) {
            margin: 0;
            max-width: 200px;
        }
    }
    &:last-child {
        z-index: 2;
    }
    &_img {
        position: absolute;
        margin: 0 30px;
        height: 300px;
        bottom: -10px;
        @media(max-width: 620px) {
            display: none;
        }
        img {
            height: 200px;
        }
    }
}