.double-col-block {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    @media(max-width: 1024px) {}
    .double-block-inner {
        display: flex;
        justify-content: space-between;
        padding: 50px;
        border-radius: 5px;
        gap: 1rem;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, .2);
        border-radius: 5px;
        margin-top: 10px;
        @media(max-width: 1024px) {
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            margin-left: 50px;
            margin-right: 50px;
            padding: 30px;
        }
        @media(max-width: 620px) {
            margin-right: 10px;
            margin-left: 10px;
            padding: 10px;
        }
    }
    .text-block {
        background: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(146, 146, 146, 0.25);
        border-radius: 5px;
        padding: 40px 30px;
        max-width: 560px;
        flex-shrink: 0;
        width: 100%;
        @media(max-width: 620px) {
          padding: 10px;
        }
        h4 {
            font-size: 25px;
            margin-bottom: 30px;
            font-weight: normal;
            @media(max-width: 767px) {
                font-size: 21px;
            }
            @media(max-width: 580px) {
              font-size: 18px;
            }
        }
        p {
            font-size: 16px;
            line-height: 25px;
            margin-bottom: 30px;
            @media(max-width: 580px) {
              font-size: 14px;
          }
        }
    }
    .image-block {
        max-width: 560px;
        max-height: 446px;
        width: 100%;
        border-radius: 5px;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            border-radius: 5px;
            position: absolute;
        }
    }
    .double-col-btn {
        background-color: transparent;
        border: 1px solid #0074bc;
        color: #0074bc;
        padding: 10px 20px;
        font-weight: normal;
        cursor: pointer;
        transition: all .2s linear;
        border-radius: 3px;
        a {
            color: #0074bc;
            transition: all .2s linear;
        }
        &:hover {
            background-color: #0074bc;
            border-color: #0074bc;
            color: #fff;
            transition: all .2s linear;
            a {
                border-color: #0074bc;
                color: #fff;
                transition: all .2s linear;
            }
        }
    }
}


.souble-block-parallax {
  background-image: url('../../../img/anaeroby.webp');
}
.image-block-layer {
  background-color: rgba(255, 255, 255, .2);
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 5px;
}
.image-block-layer-two {
  background-color: rgba(0, 116, 188, .2);
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 5px;
}

.blue {
    background: #0074bc;
}

.red {
    background: #FFDBD4;
}

.grey {
    background: linear-gradient(140.32deg, rgba(128, 153, 193, 0.11) 33.15%, rgba(182, 182, 182, 0.18) 75.57%, rgba(182, 182, 182, 0.22) 97.57%);;
}

.block-reverse {
    flex-direction: row-reverse;
}


/*btn */

.custom-btn {
    width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 3px;
    padding: 10px 25px;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    outline: none;
    a {
        color: #fff;
    }
    span {
        color: #fff
    }
  }

.btn-3 {
    background: rgb(0,172,238);
    background: linear-gradient(0deg, #0074bc 0%, #0074bc 100%);
    width: 130px;
    height: 40px;
    line-height: 42px;
    padding: 0;
    border: none;
    
  }
  .btn-3 span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
  }
  .btn-3:before,
  .btn-3:after {
    position: absolute;
    content: "";
    right: 0;
    top: 0;
     background: #0074bc;
    transition: all 0.3s ease;
  }
  .btn-3:before {
    height: 0%;
    width: 2px;
  }
  .btn-3:after {
    width: 0%;
    height: 2px;
  }
  .btn-3:hover{
     background: transparent;
    box-shadow: none;
    a {
        color: #292930;
    }
  }
  .btn-3:hover:before {
    height: 100%;
  }
  .btn-3:hover:after {
    width: 100%;
  }
  .btn-3 span:hover{
     color: rgba(2,126,251,1);
  }
  .btn-3 span:before,
  .btn-3 span:after {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
     background: rgba(2,126,251,1);
    transition: all 0.3s ease;
  }
  .btn-3 span:before {
    width: 2px;
    height: 0%;
  }
  .btn-3 span:after {
    width: 0%;
    height: 2px;
  }
  .btn-3 span:hover:before {
    height: 100%;
  }
  .btn-3 span:hover:after {
    width: 100%;
  }
