.videos-inner {
    max-width: 1200px;
    margin: 0 auto;
}

.video-previews {
    position: relative;
    justify-content: space-between;
    &-item {
        max-width: 400px;
        width: 100%;
        cursor: pointer;
        transition: all .2s linear;
        background: linear-gradient(140.32deg, rgba(128, 153, 193, 0.11) 33.15%, rgba(182, 182, 182, 0.18) 75.57%, rgba(182, 182, 182, 0.22) 97.57%);
        border-radius: 5px;
        padding: 40px 20px;
        margin: 15px;
        transition: all .2s linear;
        box-shadow: 0 0 10px 2px rgba(0, 0, 0, .2);
        img {
            width: 100%;
            border-radius: 5px;
        }
        &:hover {
            transition: all .2s linear;
            transform: scale(0.988) !important;
        }

    }
    .slick-next-videos {
        right: 0px;
    }
    .slick-prev-videos {
        left: 0px;
    }
}

.video-preview-info {
    margin-top: 30px;
    &_date {
        margin-bottom: 20px;
        display: flex;
        justify-content: flex-end;
        font-size: 14px;
        color: #292930        ;
    }
    &_name {
        font-size: 25px;
    }
}