.news-inner {
    padding: 50px;
    border-radius: 5px;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, .2);
    @media(max-width: 620px) {
        padding: 10px;
    }
}

.news-switcher {
    display: flex;
    margin-bottom: 20px;
}

.news-row {
    display: flex;
    justify-content: space-between;
    .news-col {
        max-width: 600px;
        @media(max-width: 768px) {
            margin-bottom: 10px;
        }
    }
    @media(max-width: 999px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.news-switcher-item {
    background-color: #F0F0F5;
    color: #000;
    padding: 10px 30px;
    border: none;
    &:not(:last-child) {
        margin-right: 10px;
    }
}
.news-switcher-item.active {
    background-color: #C8D6ED;
}

.news-list {
    display: flex;
    justify-content: space-between;
    &-main {
        max-width: 600px;
        max-height: 360px;
        height: 100%;
        width: 100%;
        position: relative;
        color: #fff;
        overflow: hidden;
        flex-shrink: 0;
        border: 1px solid #eee;
        border-radius: 5px;
        border-bottom: 0;
        @media(max-width: 400px) {
            max-height: 100%;
        }
        h4 {
            font-size: 20px;
            transition: all .5s linear;
            font-weight: normal;
            margin-bottom: 10px;
            @media(max-width: 768px) {
                font-size: 16px;
            }
        }
        &-info {
            position: absolute;
            bottom: 0;
            left: 10px;
            right: 10px;
            padding: 20px;
            z-index: 20;
            background-color: rgba(255,255,255, .5);
            color: #000;
            box-shadow: 0 0 5px 5px rgba(0, 0, 0, .3);
            border-radius: 5px;
            bottom: 10px;
            @media(max-width: 400px) {
                position: relative;
                left: 0;
                right: 0;
                margin-right: 5px;
                margin-left: 5px;
                bottom: 10px;
                padding: 5px;
            }
            p {
                font-size: 14px;
            }
        }
        img {
            width: 100%;
            height: auto;
            transition: all .2s linear;
        }
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(0, 116, 188, 0.33);
            z-index: 1;
            transition: all .2s linear;
            z-index: 1;
        }
        &:hover {
            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: rgba(0, 116, 188, 0.23);
                z-index: 1;
                transition: all .2s linear;
                z-index: 1;
            }
            img {
                transform: scale(1.01);
                transition: all .2s linear;
            }
        }
    }
}

.news-secondary {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.news-secondary-item {
    max-width: 280px;
    width: 100%;
    overflow: hidden;
    border: 1px solid #eee;
    box-shadow: 0 0 8px 1px rgb(0, 0, 0, 0.2);
    &:first-child {
        border-bottom-left-radius: 5px;
    }
    &:last-child {
        border-bottom-right-radius: 5px;
    }
    &_info {
        padding: 10px;
    }
    h4 {
        font-weight: normal;
        font-size: 14px;
        transition: all .2s linear;
    }
    .visitors {
        color: #8D8D92;
        font-size: 12px;
        display: flex;
        align-items: center;
        img {
            margin-right: 5px;
        }
    }
    .date {
        color: #8D8D92;
        font-size: 12px;
        margin-right: 10px;
    }
    .news-list-date {
        display: flex;
        margin-top: 20px;
    }
    &__img {
        height: 200px;
        width: 100%;
        img {
            transition: all .2s linear;
        }
    }
    &:hover {
        .news-secondary-item_info {
            h4 {
                color: #8D8D92;
                transition: all .2s linear;
            }
        }
        .news-secondary-item__img {
            img {
                transform: scale(1.01);
                transition: all .2s linear;
            }
        }
    }
}

.article-list {
    padding: 0 20px;
    overflow-y: auto;
    max-height: 360px;
    max-width: 600px;
    padding-top: 10px;
    @media(max-width: 540px) {
        padding: 0 5px;
    }
}

.article-list-item {
    background-color: rgba(255, 255, 255, .8);
    width: 100%;
    max-width: 700px;
    transition: all .2s linear;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 20px 10px 20px 10px;
    margin-bottom: 20px;
    position: relative;
    border: 1px solid #eee;
    border-radius: 5px;
    max-height: 160px;
    height: 100%;
    h4 {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;
    }
    &:hover {
        box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
    }
    p {
        font-size: 14px;
    }
    img {
        margin-right: 20px;
        @media(max-width: 380px) {
            display: none;
        }
    }
    &:hover {
        transform: scale(1.01);
        transition: all .2s linear;
    }
    @media(max-width: 540px) {
       p {
        font-size: 12px;
       }
       h4 {
        font-size: 14px;
       }
    }
    
}

.article-list::-webkit-scrollbar {
    width: 2px;               /* ширина scrollbar */
  }
.article-list::-webkit-scrollbar-track {
    background: #eee;        /* цвет дорожки */
  }
.article-list::-webkit-scrollbar-thumb {
    background-color: #0074BC;    /* цвет плашки */
    border-radius: 20px;       /* закругления плашки */
  }

