.popular {
    position: relative;
    margin-top: -20px;
    @media(max-width: 1200px) {
        margin-top: 50px;
    }
    &-item {
        width: 300px;
        background: #FFFFFF;
        box-shadow: 0px 0px 10px 0px rgba(39, 92, 141, 0.5);
        margin: 0 10px;
        padding: 20px;
        margin-bottom: 20px;
        border-radius: 5px;
        transition: all .2s linear;
        margin-top: 10px;
        &:hover {
            transform: translateY(10px) !important;
            box-shadow: 0px 0px 10px 0px rgba(0, 116, 188, 0.8);
            transition: all .2s linear !important;
        }
    }
    &-image-container {
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
        img {
            max-height: 150px;
            height: 100%;
            max-width: 100%;
            width: auto;
            text-align: center;
        }
    }
    &-text-container {
        margin-bottom: 20px;
        h4 {
            font-size: 20px;
            padding-bottom: 10px;
        }
        .cutoff-text {
            font-size: 14px;

            --max-lines: 4;
            --line-height: 1.4;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: var(--max-lines);

            overflow: hidden;
            max-height: calc(var(--max-lines) * 1em * var(--line-height));
            position: relative;
            &::before {
                content: '';
                position: absolute;
                height: calc(1em * var(--line-height));
                width: 100%;
                bottom: 0;
                pointer-events: none;
                background: linear-gradient(to bottom, transparent, white);
            }
        }
    }

    .popular-btn-container {
        button {
            background-color: #0074BC;
            color: #fff;
            border: none;
            border-radius: 3px;
            width: 100%;
            padding: 10px 20px;
            transition: all .2s linear;
            &:hover {
                background-color: rgb(147, 174, 221);
                transition: all .2s linear;
            }
        }
    }
    .slick-dots {
        display: flex;
        position: absolute;
        left: 50%;
        margin-left: -30px;
        button {
            display: none;
        }
        li {
            display: block;
            height: 10px;
            width: 10px;
            border-radius: 50%;
            margin: 10px;
            background-color: #D6DAE4;
            cursor: pointer;
        }
        .slick-active {
            background-color: #0074BC;
        }
    }
}

.popular {
    .slick-prev {
        left: -10px;
    }
    .slick-next {
        right: -10px;
    }
}