.slick-prev, .slick-next {
    cursor: pointer;
    height: 50px;
    width: 50px;
    position: absolute;
    @media (max-width: 1400px) {
        width: 30px;
        height: 30px;
    }
}
.slick-prev {
    left: 30px;
    z-index: 999;
    top: 45%;
    @media (max-width: 1400px) {
        left: 5px;
    }
    @media (max-width: 1300px) {
        left: 0px;
    }
}
.slick-next {
    right: 30px;
    z-index: 999;
    top: 45%;
    @media (max-width: 1400px) {
        right: 5px;
    }
    @media (max-width: 1300px) {
        right: 0px;
    }
}

.slider {
    picture {
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: linear-gradient(90deg, rgba(0, 116, 188, 0.8) 0%, rgba(0, 116, 188, 0.7) 28.08%, rgba(96, 168, 213, 0.21) 67.91%, rgba(255, 255, 255, 0.3) 115.46%);
            z-index: 9;
        }
    }
    .slick-dots {
        display: flex !important;
        position: absolute;
        left: 50%;
        bottom: 10%;
        margin-left: -45px;
        button {
            display: none;
        }
        li {
            display: block;
            height: 10px;
            width: 10px;
            border-radius: 50%;
            margin: 10px;
            background-color: #D6DAE4;
            cursor: pointer;
        }
        .slick-active {
            background-color: #0074BC;
        }
    }
}

.slider-img {
    position: relative;
}
.banner-info {
    position: absolute;
    z-index: 999999;
    bottom: 15%;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    h4 {
        margin-bottom: 20px;
        font-size: 35px;
        font-weight: bold;
        color: #fff;
        text-transform: uppercase;
        position: relative;
        width: fit-content;
        @media (max-width: 768px) {
            font-size: 25px;
            text-align: center;
        }
        // &:after {
        //     content: '';
        //     display: block;
        //     height: 3px;
        //     width: 100%;
        //     background-color: #fff;
        //     position: absolute;
        //     bottom: -10px;
        // }
    }
    &__inner {
        background-color: transparent;
        border-radius: 5px;
        padding: 30px;
        @media (max-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
        }
    }
    .banner-info-item {
        font-size: 18px;
        display: flex;
        align-items: center;
        padding-bottom: 30px;
        margin-bottom: 50px;
        .banner-info-item__text {
            color: #fff;
            font-size: 30px;
            @media (max-width: 768px) {
                font-size: 18px;
            }
        }
        img {
            height: 20px;
            width: 20px;
            margin-right: 10px;
        }
    }
}

.banner-info-item__list {
    background: #fff;
    padding: 10px;
    color: #000;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: 4px solid #0074bc;
    font-size: 24px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}


.banner-btn {
    max-width: 120px;
    display: flex;
    align-items: center;
    position: relative;
    font-size: 20px;
    cursor: pointer;
    
    span {
        margin-right: 20px;
        color: #fff;
        @media (max-width: 768px) {
            font-size: 18px;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            height: 2px;
            width: 0;
            opacity: 0;
            bottom: -10px;
            transition: all .2s linear;
            pointer-events: none;
        }
        &::before {
            content: '';
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background-color: #fff;
            right: 0;
            opacity: 1;
            bottom: -10px;
            transition: all .2s linear;
            pointer-events: none;
        }
    }
    .banner-btn-img {
        pointer-events: none;
        width: 50px;
        transition: all .2s linear;
        path {
            stroke: #ff4242;
        }
    }
    &:hover {
        span::after {
            width: 100%;
            opacity: 1;
            transition: all .2s linear;
        }
        span {
            color: #fff;
            transition: all .2s linear;
        }
    }
    &:hover {
        .banner-btn-img {
            transform: translateX(30px);
            transition: all .2s linear;
        }
    }
}
