
.industry-solutions {
    max-width: 1200px;
    margin: 0 auto;
    .row {
        display: flex;
        justify-content: space-between;
        padding-bottom: 5px;
        gap: 0.1rem;
        margin-bottom: 0.1rem;
        @media(max-width: 1024px) {
            flex-wrap: wrap;
            margin: 0px 50px;
            margin-bottom: 0.1rem;
        }
        @media(max-width: 620px) {
            margin-right: 10px;
            margin-left: 10px;
        }
    }
    .short {
        width: 20%;
        height: 250px;
        overflow: hidden;
        flex-shrink: 0;
        @media(max-width: 1024px) {
            width: 49.8%;
            border-radius: 5px;
        }
        @media(max-width: 620px) {
            width: 100%;
        }
        img {
            width: 100%;
            height: 100%;
        }
    }
    .wide {
        width: 59%;
        overflow: hidden;
        height: 250px;
        flex-shrink: 0;
        @media(max-width: 1024px) {
            width: 100%;
            border-radius: 5px;
        }
        img {
            width: 100%;
            height: auto;
        }
    }
    .half-width {
        width: 49.8%;
        height: 250px;
        @media(max-width: 1024px) {
            width: 100%;
            border-radius: 5px;
        }
    }
    .row.row-reverse {
        flex-direction: row-reverse;
    }
    .solution-item {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        transition: all .2s linear;
        .industry-solutions-btn {
            border: 1px solid #fff;
            background-color: transparent;
            color: #fff;
            padding: 10px 20px;
            transition: all .2s linear;
            border-radius: 3px;
            a {
                color: #fff;
            }
        }
        &-inner {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h4 {
                font-size: 23px;
                color: #fff;
                margin-bottom: 20px;
                z-index: 2;
                transition: all .2s linear;
                text-align: center;
            }
        }
        .fade {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(180deg, rgba(0, 116, 188, 0.33) 0%, rgba(0, 116, 188, 0.33) 86.46%, rgba(0, 0, 0, 0) 100%);
            z-index: 1;
            transition: all .5s linear;
        }
        &:hover {
            h4 {
                z-index: 2;
                transition: all .2s linear;
                text-shadow: #fff 1px 0 10px;
            }
            .industry-solutions-btn {
                z-index: 2;
                transition: all .5s linear;
                box-shadow: 0 0 20px 10px rgba(255, 255, 255, .8);
                background-color: #fff;
                color: #000;
                a {
                    color: #000;
                }
            }
            .fade {
                background: linear-gradient(180deg, rgba(0, 116, 188, 0.23) 0%, rgba(0, 116, 188, 0.23) 86.46%, rgba(0, 0, 0, 0) 100%);
                transition: all .5s linear;
            }
        }
    }
    .hidden {
        display: none;
        transition: all .5s linear;
    }
    .show-more-container {
        width: 100%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        padding: 40px;
        padding-bottom: 0;
    }
    .show-more {
        display: inline-block;
        background-color: transparent;
        color: #0074BC;
        border: 1px solid #0074BC;
        font-size: 20px;
        padding: 10px 30px;
        transition: all .2s linear;
        border-radius: 3px;
        &:hover {
            background-color: #0074BC;
            color: #fff;
            transition: all .2s linear;
        }
    }
}


