.dealers {
    background-image: url('../../../img/dealers.png');
    background: linear-gradient(180deg, #252525 0%, rgba(0, 116, 188, 0) 100%), url('../../../img/dealers.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    &-inner {
        display: flex;
        align-items: center;
        width: 100%;
        max-width: 1080px;
        margin: 0px auto;
        padding: 50px;
        justify-content: space-between;
        align-items: center;
        height: 300px;
        @media(max-width: 780px) {
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
        }
    }
    &-text-col {
        color: #fff;
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        @media(max-width: 780px) {
            padding-bottom: 10px;
        }
        @media(max-width: 380px) {
            font-size: 21px;
        }
    }
}